






















































import Vue from 'vue';
import NumberPlate from '../components/NumberPlate.vue';
import MapCurrentLocation from '../components/MapCurrentLocation.vue';
import LocationModel from '../models/LocationModel';
import Buefy from 'buefy';

Vue.use(Buefy);

export default Vue.extend({
    name: 'AddForm',
    props: {
        plate: {
            type: Number,
            default: 0,
        },
        teamEffort: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            date: new Date(),
            location: new LocationModel(),
            seenBy: [],
            note: '',
            submitted: false,
            errorMsg: null,
        };
    },
    components: {
        NumberPlate,
        MapCurrentLocation
    },
    computed: {
        submittable() {
            if (!this.date) {
                return false;
            }
            if (!this.location || this.location.Lat == 0 || this.location.Lng == 0) {
                return false;
            }
            if (this.teamEffort && this.seenBy.length == 0) {
                return false;
            }
            return true;
        }
    },
    methods: {
        save() {
            if (this.teamEffort && this.seenBy.length === 0) {
                this.$data.errorMsg = 'Du måste ange vilka som såg den!';
                return;
            }

            this.$data.errorMsg = null;

            this.$emit('create', {
                plate: this.plate,
                date: this.date.toISOString().split('T')[0],
                location: this.location,
                seenBy: this.seenBy,
                note: this.note,
            });
            this.submitted = true;
        },
        resetSubmitted() {
            this.submitted = false;
        },
        updateLocation(newLocation : LocationModel) {
            this.location = newLocation;
        }
    },
    mounted() {
        this.resetSubmitted();
    }
});
